body, body * {
  margin: 0;
  padding: 0;
  vertical-align: top;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.bold {
  font-weight: bold;
}

.normal {
  font-weight: normal;
}

.underline{
  text-decoration: underline;
}

